* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: IRANSans;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: #2F3542;
  font-weight: 300;
  transition: ease 140ms;
}

a:hover,
a:focus,
a:active {
  color: #747D8C;
}